@keyframes main-animation {
  from {
    transform: scale(1);
  }
  to {
    transform: scale(0.5);
  }
}

@keyframes height-s {
  from {
    height: 100%;
  }
  to {
    height: 100vh;
  }
}

@keyframes appear {
  from {
    opacity: 0;
    transform: translateX(20%);
  }
  to {
    opacity: 1;
    transform: translateX(0px);
  }
}

@keyframes hide {
  0% {
    opacity: 1;
    transform: translateX(0px);
  }
  80% {
    opacity: 0;
    transform: translateX(20%);
  }
  100% {
    opacity: 0;
    transform: translateX(500%);
  }
}

@keyframes scroll-lead {
  0% {
    transform: scale(1, 0);
    transform-origin: 0 0;
  }
  50% {
    transform: scale(1, 1);
    transform-origin: 0 0;
  }
  51% {
    transform: scale(1, 1);
    transform-origin: 0 100%;
  }
  100% {
    transform: scale(1, 0);
    transform-origin: 0 100%;
  }
}

@keyframes block-appear {
  from {
    opacity: 0;
    transform: translateY(15%);
  }
  to {
    opacity: 1;
    transform: translateY(0%);
  }
}

@keyframes noise-1 {
  0%, 20%, 40%, 60%, 70%, 90% {
    opacity: 0;
  }
  10% {
    opacity: .1;
  }
  50% {
    opacity: .5;
    left: -6px;
  }
  80% {
    opacity: .3;
  }
  100% {
    opacity: .6;
    left: 2px;
  }
}

@keyframes noise-2 {
  0%, 20%, 40%, 60%, 70%, 90% {
    opacity: 0;
  }
  10% {
    opacity: .1;
  }
  50% {
    opacity: .5;
    left: 6px;
  }
  80% {
    opacity: .3;
  }
  100% {
    opacity: .6;
    left: -2px;
  }
}

@keyframes noise {
  0%, 3%, 5%, 42%, 44%, 100% {
    opacity: 1;
    transform: scaleY(1);
  }
  4.3% {
    opacity: 1;
    transform: scaleY(1.7);
  }
  43% {
    opacity: 1;
    transform: scaleX(1.5);
  }
}

@keyframes noise-3 {
  0%, 3%, 5%, 42%, 44%, 100% {
    opacity: 1;
    transform: scaleY(1);
  }
  4.3% {
    opacity: 1;
    transform: scaleY(4);
  }
  43% {
    opacity: 1;
    transform: scaleX(10) rotate(60deg);
  }
}

@keyframes grain {
  0%, 100% {
    transform: translate(0, 0);
  }
  10% {
    transform: translate(-5%, -10%);
  }
  20% {
    transform: translate(-15%, 5%);
  }
  30% {
    transform: translate(7%, -25%);
  }
  40% {
    transform: translate(-5%, 25%);
  }
  50% {
    transform: translate(-15%, 10%);
  }
  60% {
    transform: translate(15%, 0%);
  }
  70% {
    transform: translate(0%, 15%);
  }
  80% {
    transform: translate(3%, 35%);
  }
  90% {
    transform: translate(-10%, 10%);
  }
}

@keyframes marquee {
  0% {
    transform: translate3d(var(--move-initial), 0, 0);
  }
  100% {
    transform: translate3d(var(--move-final), 0, 0);
  }
}

@keyframes PageAnime {
  0% {
    transform-origin: left;
    transform: scaleX(0);
  }
  50% {
    transform-origin: left;
    transform: scaleX(1);
  }
  50.001% {
    transform-origin: right;
  }
  100% {
    transform-origin: right;
    transform: scaleX(0);
  }
}

@keyframes PageAnimeAppear {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}

html {
  /*
    計算式：10px /  デザインカンプの横幅 * 100
    画面サイズがデザインカンプ幅の時font-size = 10pxになる
    */
  font-size: calc(10 / 1920 * 100)vw;
}

@media (max-width: 768px) {
  html {
    font-size: calc(10 / 768 * 100)vw;
  }
}

@media (max-width: 375px) {
  html {
    font-size: calc(10 / 375 * 100)vw;
  }
}

body {
  font-size: 1.6rem;
  font-family: "Hiragino Kaku Gothic ProN", "Hiragino Sans", "游ゴシック体", YuGothic, "游ゴシック", "Yu Gothic", Meiryo, sans-serif;
  letter-spacing: 0.1em;
  overflow-x: hidden;
  position: relative;
  line-height: 1;
}

img {
  width: 100%;
  height: 100%;
  object-fit: cover;
  vertical-align: middle;
}

.container {
  width: 1648px;
  margin: 0 auto;
}

.header {
  width: 100vw;
  position: fixed;
  top: 0;
  left: 0;
  z-index: 100;
}

.header__inner {
  display: flex;
  height: 11rem;
  align-items: center;
}

.header__logo {
  font-size: 4.8rem;
  color: #ffffff;
  font-weight: bold;
  font-family: YuMincho, "Yu Mincho", serif;
  letter-spacing: 0.1em;
}

.header__global-navi {
  margin-left: auto;
  display: flex;
  position: absolute;
  right: 0;
}

.header__navi-item {
  margin-right: 4.1rem;
}

.header__navi-item:last-child {
  margin-right: 0;
}

.header__link {
  font-size: 1.6rem;
  color: #ffffff;
  font-weight: 300;
  text-transform: uppercase;
}

.header__cta {
  padding: 4.5rem 7.6rem;
  background-color: #735500;
}

.main-visual {
  position: relative;
  width: 100%;
  height: 98rem;
}

.main-visual__swiper {
  width: 100%;
  height: 100%;
}

.main-visual .next-prev {
  position: absolute;
  right: 0;
  bottom: 0;
  display: flex;
}

.main-visual .swiper-button-prev, .main-visual .swiper-button-next {
  width: 10rem;
  height: 10rem;
  background-color: #000000;
}

.main-visual .swiper-button-prev:hover, .main-visual .swiper-button-next:hover {
  background-color: #735500;
}

.main-visual .swiper-button-prev:after, .main-visual .swiper-button-next:after {
  color: #ffffff;
}

.main-visual .swiper-button-prev {
  margin-right: 1.6rem;
}

.main-visual-contents {
  position: absolute;
  top: 50%;
  left: 6.9%;
  transform: translateY(-50%);
  z-index: 10;
}

.main-visual-contents__glass {
  position: relative;
  width: 70rem;
  height: 56rem;
  color: white;
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 20px;
  border-radius: 0px;
  backdrop-filter: blur(16px);
  background-color: rgba(255, 255, 255, 0.3);
  box-shadow: rgba(0, 0, 0, 0.3) 2px 8px 8px;
  border: 2px rgba(255, 255, 255, 0.4) solid;
  border-bottom: 2px rgba(40, 40, 40, 0.35) solid;
  border-right: 2px rgba(40, 40, 40, 0.35) solid;
}

.main-visual-contents__place {
  position: absolute;
  left: 0;
  top: 11%;
  overflow-x: hidden;
  max-width: 48rem;
}

.main-visual-contents__place__name {
  font-size: 3.2rem;
  color: #ffffff;
  font-weight: 700;
  text-transform: uppercase;
  display: block;
  padding: 1.3rem 2.6rem;
  letter-spacing: 0.1em;
  background-color: #000000;
}

.main-visual-contents__copy {
  font-size: 6.4rem;
  color: #ffffff;
  font-weight: normal;
  font-family: "Nothing You Could Do", cursive;
  line-height: 1.5;
  text-transform: uppercase;
  text-shadow: 1px 0 1.3rem rgba(0, 0, 0, 0.32);
  display: block;
}
